import { StaticSpinner } from "@rp/react/web/icons"
import { Box } from "@rp/react/web/mui/core"

type Props = { size: number; drag: number }

const RefreshIndicator = ({ size, drag }: Props) => {
  const top = -size + Math.min(size + 5, drag)
  const opacity = Math.max(0, (drag - size + 5) / 100)
  return (
    <Box sx={{ position: "relative", width: `${size}px`, margin: "0 auto", opacity }}>
      <Box sx={{ zIndex: 3000, width: `${size}px`, height: `${size}px`, position: "fixed", top }}>
        <StaticSpinner
          style={{
            zIndex: 3000,
            width: `${size}px`,
            height: `${size}px`,
            transform: `rotate(${drag * 1.5}deg)`,
          }}
        />
      </Box>
    </Box>
  )
}

export { RefreshIndicator }
